/** @jsx jsx */
import { jsx } from "theme-ui"
import BlogListItem from "./blog-list-item"


type ListingProps = {
  posts: {
    slug: string
    title: string
    date: string
    excerpt: string
    description: string
    timeToRead?: number
    body: string
    tags?: {
      name: string
      slug: string
    }[],
    frontmatter: {
      [key: string]: any
    }
  }[]
  className?: string
  showTags?: boolean
  selectedCategory: string
}



const Listing = ({ posts, className = ``, showTags = true, selectedCategory = '' }: ListingProps) => {
  let count = 0
  return (
    <section className="mx-auto">

      {
        selectedCategory == '' ?
          posts.map((post, index) => {
            count === 10 ? count = 1 : count += 1

            return <BlogListItem key={post.slug + index} post={post} showTags={showTags} imgNumber={count} />
          })
          :

          posts.map((post, index) => {
            count === 10 ? count = 1 : count += 1
            if (post.frontmatter.category == selectedCategory) {
              return (
                <BlogListItem key={post.slug + index} post={post} showTags={showTags} imgNumber={count} />
              )
            }

          })


      }


    </section>
  )
}

export default Listing
