import { graphql } from "gatsby"
import React from "react";
import BlogComponent from "../components/blog"


const BlogPage = ({ data }) => {
  const mergedPosts = data.allPost.nodes.map((post, index) => {
    const { frontmatter } = data.allMdx.nodes.find(fm => post.slug === fm.frontmatter.slug);
    
    return {
      ...post,
      frontmatter
    }
  })
  return (<BlogComponent posts={mergedPosts} />)
}

export default BlogPage

export const query = graphql`
  query($formatString: String!) {
    allPost(sort: { fields: date, order: DESC }) {
      nodes {
        slug
        title
        date(formatString: $formatString)
        body
        excerpt
        description
        tags {
          name
          slug
        }
      }
    }
    allMdx(sort: { fields: frontmatter___date, order: DESC} ) {
      nodes {
        frontmatter {
          date
          slug
          author
          email
        }
      }
    }
  }
`